@use "../abstracts/variables";
@use "../abstracts/mixins";
@use "../components/buttons";

.menu-mobil {
  display: none;
}

.menu-desktop {
  & > *:not(:last-child) {
    margin-right: 15px !important;

    @include mixins.mediaCustom("min-width: 1250px") {
      margin-right: 30px !important;
    }
  }
}

%flex-center {
  display: flex;
  align-items: center;
}

%reset-buttons {
  padding: 0;
  border-style: none;
  margin: 0;
}

button {
  background-color: transparent;
  border-style: none;
  padding: 0;
  margin: 0;
}

%underline:after {
  @include mixins.mediaCustom("min-width: 1100px") {
    height: 3px;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: variables.$color-principal;
    content: "";
    transform: scaleX(0);
    transform-origin: bottom right;
    transition: transform 0.3s ease-out;
  }
}

%logoChangeColor {
  .header__logo .logo--dark {
    display: block;
  }

  .header__logo .logo--light {
    display: none;
  }
}

.header__container {
  color: var(--bg-header-text-color);
  font-size: variables.$font-size--general-up-s;
  line-height: variables.$line-height--general-up-s;
  box-shadow: var(--shadow-header);

  &.down {
    box-shadow: unset;
    z-index: -1;
  }

  .black {
    .logo--dark {
      display: block;
    }

    .logo--light {
      display: none;
    }
  }

  .white {
    .logo--dark {
      display: none;
    }

    .logo--light {
      display: block;
    }
  }

  .header--notindex {
    background-color: variables.$color-white;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);

    .logo--light {
      display: none;
    }

    .btn-1 {
      color: variables.$color-white;
    }

    .btn-1:hover {
      color: variables.$color-black;
    }
  }

  & > .header__components {
    overflow-x: hidden;
    transition: transform 0.3s;
  }

  &.sticky-header .header__components > .header {
    width: 100%;
    background-color: variables.$color-white;
    color: variables.$color-black;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);

    .btn-1 {
      color: variables.$color-white;
    }

    .btn-1:hover {
      color: variables.$color-black;
    }

    @extend %logoChangeColor;
  }

  &.down > .header__components {
    transform: translateY(-100%);
  }

  &:hover .header__components > .header {
    background-color: variables.$color-white;
    color: variables.$color-black;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);

    .btn-1 {
      color: variables.$color-white;
    }

    .btn-1:hover {
      color: variables.$color-black;
    }

    @extend %logoChangeColor;
  }

  &.isOpenMobile {
    background-color: variables.$color-white;
    color: variables.$color-black;

    &.down .header__components > .header {
      transform: unset;
    }
  }

  .btn-1 {
    @extend .btn-1;
  }

  .header {
    background-color: var(--bg-header);

    @include mixins.mediaCustom("max-width: 1099px") {
      grid-template-columns: fit-content(20px) 1fr fit-content(60px);
    }
  }

  .white-bg.logo--dark {
    display: block;
  }

  .white-bg.logo--light {
    display: none;
  }
}

.header {
  padding: 0 50px;
  z-index: 2;
  transition-property: background-color, transform;
  transition-duration: 0.3s;
  --columns: 2fr 10fr 2fr;
  --column-gap: 15px;

  @include mixins.mediaCustom("min-width: 1350px") {
    --columns: 2fr 7fr 2fr;
  }

  a {
    color: inherit;
    text-decoration: none;
  }

  &__logo {
    @extend %flex-center;
    position: relative;
    z-index: 1;

    @include mixins.mediaCustom("max-width: 1099px") {
      flex-direction: row-reverse;
    }

    a {
      display: block;
      font-weight: 600;
      letter-spacing: 3px;
      text-decoration: none;
      text-transform: uppercase;
      line-height: 0;

      @include mixins.mediaCustom("max-width: 1099px") {
        margin: 0 auto;
        transform: translateX(7.14%);
      }
    }

    .header__predictive-icon {
      height: 22px;
      width: 22px;
      @include mixins.clickEffect;

      @include mixins.mediaCustom("min-width: 1100px") {
        margin-left: 35px;
      }
    }
  }

  .header__user-icon-cart {
    position: relative;
  }

  .button-cart__count {
    position: absolute;
    top: -5px;
    right: -5px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: variables.$color-accent-3;
  }

  & > nav {
    display: flex;
    justify-content: center;

    .burger-nav {
      display: none;
      @extend %reset-buttons;
      @include mixins.clickEffect;

      svg {
        width: 20px;
        display: block;
      }

      .icon-hamburger {
        width: 23px;
        height: 18px;
      }

      .icon-close {
        width: 13.33px;
      }
    }

    #nav-list-movil-js {
      @include mixins.media("phone") {
        width: calc(100% - 65px);
        max-width: 400px;
      }
    }

    ul .burger-nav {
      margin-left: auto;
      padding: 0 variables.$paddings-side--movil 22px;
    }

    ul {
      list-style: none;
    }

    & > ul > li.nav-item {
      @include mixins.mediaCustom("min-width: 1100px") {
        padding: 18px 0 0 0;
        flex-direction: column;
        justify-content: space-between;
      }

      &--btn {
        padding: 8px 0;
      }

      .underline {
        display: none;
        @extend %underline;

        @include mixins.mediaCustom("min-width: 1100px") {
          position: relative;
          display: inline-flex;
          margin: 0;
          padding-bottom: 18px;
          width: 100%;
        }
      }

      &:hover {
        .nav-item__link {
          text-shadow: -0.25px -0.25px 0 variables.$color-black, 0.25px 0.25px variables.$color-black;
          transition: text-shadow 0.3s ease-in-out;

          &--span {
            text-shadow: -0.25px -0.25px 0 variables.$color-principal, 0.25px 0.25px variables.$color-principal;
          }
        }
      }

      &:hover .underline:after,
      .underline:hover:after {
        @include mixins.mediaCustom("min-width: 1100px") {
          /* If blue line needed on hover uncomment next line */
          // transform: scaleX(1);
          transform-origin: bottom left;
        }
      }

      .underline-dummy {
        padding-bottom: 18px;
        margin: 0;
      }

      &--blue > button {
        color: variables.$color-principal;
        font-weight: 400;
        text-decoration: underline;
      }

      &--type-blog {
        display: none;
      }
    }

    & > ul,
    .menu-mobil {
      --justify: center;
      --gap-flex: 30px;
      --align: stretch;
      flex-wrap: wrap;

      & > li,
      .nav-item-child {
        text-transform: uppercase;
        padding: 20px 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        cursor: pointer;

        & > * {
          margin: auto 0;
        }

        & > a {
          text-decoration: none;
        }

        .dropdown > div {
          @include mixins.mediaCustom("min-width: 1100px") {
            transition: opacity ease 0.45s;
            opacity: 0;
          }
        }

        & > button {
          @extend %flex-center;
          text-transform: inherit;
          @include mixins.clickEffect;

          svg {
            height: 5px;
            color: inherit;
            transition: transform ease 0.3s;

            @include mixins.mediaCustom("min-width: 1100px") {
              display: none;
            }
          }

          &[data-active="true"] {
            & ~ div.dropdown {
              height: auto;
              padding: 15px 0 0 0;
              $just-space: 30px;
              overflow-y: auto;

              @include mixins.scroll(variables.$color-background-3, 5px, 5px, variables.$color-background-5);

              @include mixins.mediaCustom("min-width: 1100px") {
                padding: 20px 127px;
              }

              @include mixins.mediaCustom("min-width: 1440px") {
                height: auto;
              }
            }

            & ~ div.dropdown > div {
              @include mixins.mediaCustom("min-width: 1100px") {
                opacity: 1;
              }
            }

            & ~ div.dropdown-child {
              display: block;
            }

            svg {
              transform: rotateZ(180deg);
            }
          }
        }

        .dropdown-child {
          display: none;
        }

        .dropdown {
          position: absolute;
          top: 100%;
          left: 0;
          height: 0;
          padding: 0 127px;
          width: calc(100%);
          background-color: #fff;
          box-shadow: 0px 15px 10px rgba(0, 0, 0, 0.2);
          overflow: hidden;
          transition: all 0.4s;

          & > .grid {
            &.grid-1 {
              grid-template-columns: 100%;
            }

            &.grid-2 {
              grid-template-columns: 50% 50%;
            }

            &.grid-3 {
              grid-template-columns: repeat(3, 1fr);
            }

            &.grid-type-column {
              grid-template-columns: repeat(8, 1fr);
              grid-template-areas: variables.$area-menu-column;
              column-gap: 80px;

              .grid-3 {
                grid-template-columns: repeat(3, 1fr);
              }
            }

            .column-items {
              grid-area: items;
            }
            .column-image {
              grid-area: image;
            }
            .column-icons {
              grid-area: icons;
            }

            &.grid-type-column.columsWithoutIcons {
              grid-template-areas: unset;
              grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
              column-gap: 20px;

              &.image-count-1.items-count-2 {
                grid-template-columns: 2fr 2fr;
              }

              &.image-count-1.items-count-2 .column-image {
                width: 65%;
                margin: 0 auto;
              }

              &.image-count-2.items-count-1 {
                grid-template-columns: 1fr 2fr;
              }

              &.items-count-0.image-count-3,
              &.image-count-0.items-count-3 {
                grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
              }

              .column-items__container {
                display: grid;
                grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
                column-gap: 20px;
              }

              .column-image__container {
                display: grid;
                grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
                column-gap: 20px;
              }

              .column-items {
                grid-area: unset;
              }
              .column-image {
                grid-area: unset;
              }
              .column-icons {
                grid-area: unset;
              }
            }

            &.grid-type-column.columsWithoutIcons {
              column-gap: 20px;
            }
          }

          .column-image * {
            .nav-article {
              padding: 0;
              padding-top: 72%;

              @include mixins.media("desktop-m") {
                padding-top: 72%;
              }

              @include mixins.media("desktop-l") {
                padding-top: 70%;
              }

              @include mixins.media("desktop-xl") {
                padding-top: 66%;
              }
            }

            .item__image-content {
              @include mixins.boxFlex(center, center, column);
            }

            .item__image-text {
              margin-bottom: 24px;
              padding: 8px 34;
              color: variables.$color-principal;
              max-width: 208px;
              height: auto;

              &.bg {
                background-color: variables.$color-white;
              }
            }

            .item__image-description {
              padding: 8px 16px;
              text-align: center;
              font-size: variables.$font-size--general-m;
              line-height: variables.$line-height--general-m;
            }

            .item__image-title.flex {
              align-items: center;
              font-family: variables.$font-family--title;
              font-size: variables.$font-size--desk-h6;
              font-weight: 900;
              line-height: variables.$line-height--desk-h6;
              text-transform: uppercase;
            }

            .item__image-cta {
              max-width: 200px;
              min-width: 153px;
              font-size: variables.$font-size--general-m;
            }
          }

          div.column-icons {
            img {
              margin: 0;
            }

            a {
              display: flex;
              flex-direction: column;
            }

            ul.child-list {
              column-gap: 80px;
              row-gap: 40px;
            }

            & > ul > li > ul.child-list li {
              column-gap: 80px;
              row-gap: 40px;
              margin-bottom: 0;
            }

            ul.child-list a {
              display: block;
            }

            ul.child-list img {
              height: auto;
              width: 100%;
            }

            ul.child-list li a:after {
              content: unset;
            }

            ul li span {
              display: block;
              margin-top: 15px;
              text-align: center;
            }
          }

          ul {
            --columns: repeat(auto-fit, minmax(80px, 1fr));
            --rows: auto;
            --columns-gap: 80px;
            margin-right: 0;

            @include mixins.mediaCustom("min-width: 1100px") {
              line-height: 12px;
            }

            & > li {
              text-transform: none;

              & > a.child-title:not(.child-title--icons) {
                display: block;
                margin-bottom: 24px;
                font-weight: 300;
                text-transform: uppercase;
                text-decoration: none;
                font-size: variables.$font-size--general-up-s;
                line-height: variables.$line-height--general-up-s;

                @include mixins.mediaCustom("min-width: 1100px") {
                  margin-bottom: 18px;
                  font-family: variables.$font-family--title;
                  font-size: variables.$font-size--mobile-h6;
                  font-weight: 400;
                  line-height: variables.$line-height--mobile-h6;
                  color: variables.$color-principal;
                }
              }

              & > a.child-title--icons,
              & > ul.child-list li a {
                text-transform: uppercase;
              }

              & > a.child-title--blank {
                visibility: hidden;
              }

              & > ul.child-list li a {
                @include mixins.linkEffect(currentColor, 1px, false);
              }

              & > ul.child-list li a::after {
                bottom: -1px;
              }

              ul.child-list li:not(:last-of-type) {
                @include mixins.mediaCustom("min-width: 1100px") {
                  margin-bottom: 20px;
                }
              }
            }

            &.flex {
              --justify: space-between;

              & > li {
                margin-right: 0;
              }

              & > li > a {
                display: block;
              }

              & > li > a {
                height: auto;
                width: 100%;
              }

              & > li > a > span {
                margin-top: 15px;
                display: block;
                text-align: center;
              }
            }
          }
        }
      }
    }

    @media screen and (max-width: 1099px) {
      order: -1;
      display: flex;
      align-items: center;
      order: -1;

      .menu-desktop {
        display: none;
      }

      .burger-nav {
        display: block;
      }

      & > ul,
      .menu-mobil {
        position: fixed;
        top: 0;
        z-index: 4;
        width: 100%;
        height: 100%;
        padding: 17.33px 0;
        left: 0;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        overflow-y: auto;
        background-color: #fff;
        display: none;
        animation: slidein 0.3s ease;

        @include mixins.scroll(variables.$color-background-3, 5px, 5px, variables.$color-background-5);

        &[data-active="true"] {
          display: flex;
          flex-wrap: nowrap;

          ~ button {
            > svg {
              &.icon-close {
                display: block;
              }

              &.icon-hamburger {
                display: none;
              }
            }
          }
        }

        & > li.nav-item,
        .nav-item-child {
          display: block;
          width: 100%;
          height: auto;
          padding: 0 variables.$paddings-side--movil;
          margin: 0 0 15px;
          -webkit-tap-highlight-color: rgba(0,0,0,0);

          &--btn {
            order: 7;
          }

          &--btn a.btn-1 {
            width: 100%;
          }

          &--info {
            margin-top: auto;
            order: 10;
            @include mixins.boxFlex(space-between, initial, row);
          }

          &--info a {
            margin-left: auto;
            color: variables.$color-background-2;
            text-transform: initial;
          }

          .nav-item__lang-content ul[role="list"] {
            height: 0;
            overflow: hidden;
            padding: 0;
          }

          .nav-item__lang-content .nav-item__lang-btn {
            @include mixins.boxFlex(initial, center);
          }

          .nav-item__lang-content .nav-item__lang-btn span {
            display: none;
          }

          .nav-item__lang-content .nav-item__lang-btn svg {
            height: 10px;
            margin-left: 10px;
            width: 9px;
            transform: rotate(-90deg);
          }

          .nav-item__lang-content[data-active="true"] ul[role="list"] {
            position: absolute;
            left: 0;
            top: 0;
            padding: 0 30px;
            justify-content: flex-start;
            height: 90%;
            width: 100%;
            background-color: variables.$color-white;
            transform: translateY(52px);
          }

          .nav-item__lang-content[data-active="true"] ul[role="list"] li {
            margin-top: 48px;
          }

          .nav-item__lang-content[data-active="true"] .nav-item__lang-btn {
            position: absolute;
            top: 52px;
            z-index: 3;
          }

          .nav-item__lang-content[data-active="true"] .nav-item__lang-btn svg {
            display: flex;
            order: -1;
            width: 11px;
            height: 11px;
            margin-left: 0;
            margin-right: 20px;
            transform: rotate(90deg);
          }

          .nav-item__lang-content[data-active="true"] .nav-item__lang-btn img {
            display: none;
          }

          .nav-item__lang-content[data-active="true"] .nav-item__lang-btn span {
            display: block;
            text-transform: uppercase;
            font-weight: 400;
          }

          & > a {
            display: block;
            font-weight: 400;
          }

          & > button {
            display: flex;
            width: 100%;
            justify-content: space-between;
            font-weight: 400;

            svg {
              height: 7px;
            }
          }

          &--type-blog > a ~ * {
            display: none;
          }

          & > .dropdown,
          .dropdown-child {
            position: static;
            width: 100%;
            padding: 0;
            box-shadow: none;

            .child-list-mobil {
              margin-bottom: 10px;

              .child-title {
                font-weight: 400;
                margin: 0 16px 10px;
              }

              .item-child {
                margin: 0 16px;
              }

              .item-child a {
                display: inline-block;
              }

              .item-child:active {
                background-color: rgba(0,0,0,0.01);
              }

              .item-child:not(:last-of-type) {
                margin-bottom: 10px;
              }
            }

            .type-column {
              @include mixins.mediaCustom("max-width: 1099px") {
                display: none;
              }
            }

            ul {
              display: block;

              & > li {
                margin: 0 16px;
                color: variables.$color-background-2;
              }

              & center {
                text-align: left;
              }

              & > li:not(:last-of-type) {
                margin-bottom: 10px;
              }

              & > li > a.child-title {
                margin-bottom: 0;
              }

              & > li > ul.child-list {
                li {
                  margin: 5px 0;
                  border: 1px solid red;
                }
              }
            }
          }
        }

        @keyframes slidein {
          from {
            transform: translateX(-100%);
          }

          to {
            transform: translateX(0%);
          }
        }
      }
    }
  }

  &__user {
    position: relative;
    z-index: 1;

    & > ul {
      height: 100%;
      list-style: none;
      --justify: flex-end;
      --gap-flex: 15px;

      li {
        display: flex;
        align-items: center;

        a {
          display: block;
        }

        svg {
          height: 22px;
          display: block;
          width: 20px;
          color: inherit;
        }

        button {
          @extend %reset-buttons;
        }
      }
    }
  }

  &--center {
    --columns: 1fr;

    & > .header__logo {
      display: none;
    }

    & > .header__user {
      display: none;
    }

    & > nav {
      flex-direction: column;

      & > ul {
        --justify: space-between;
        flex-wrap: nowrap;

        & > .header__logo {
          width: 20%;
          text-align: center;

          & > a {
            margin: 0 auto;
          }
        }

        @media screen and (max-width: 1000px) {
          --gap-flex: 10px;

          & > .header__logo {
            margin: 0 20px;
          }
        }
      }
    }
  }

  @media screen and (max-width: 1099px) {
    padding: 25px variables.$paddings-side--movil;
    --columns: auto 6fr 1fr;

    &--center {
      & > .header__logo {
        display: flex;
      }

      & > .header__user {
        display: block;
      }

      & > nav > ul > .header__logo {
        display: none;
      }
    }
  }

  .btn-2 {
    background-color: var(--color-btn-bg-2);
    border: 1px solid var(--color-btn-border-2);
    color: var(--color-btn-color-2);
    width: 100%;
    padding: 10px;
    justify-content: center;
  }

  .wrapper__slider {
    & button.swiper-button {
      height: auto;
      width: auto;
    }

    & button.swiper-button-prev {
      left: -50px;
    }

    & button.swiper-button-next {
      right: -50px;
    }

    &:not(.product-slider-nav) {
      & button.swiper-button-prev {
        left: -80px;
      }

      & button.swiper-button-next {
        right: -80px;
      }
    }
  }

  .swiper-button svg {
    height: 40px;
    width: 40px;
  }

  .slider_menu_big,
  .slider_menu_short {
    .swiper-wrapper.center-elements {
      justify-content: center;
    }
  }

  .wrapper__slider.without-arrows .swiper-button {
    display: none;
  }

  .slider_menu_short {
    .product-card--head-info {
      text-transform: none;
    }

    button.swiper-button-prev {
      left: 0;
      transform: translate(-100%, -50%);
    }

    button.swiper-button-next {
      right: 0;
      transform: translate(100%, -50%);
    }
  }
  .overlay{
    background: none;
  }
}

.nav-article {
  position: relative;
  padding-top: 66%;

  & > * {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  & > img {
    object-fit: cover;
  }

  & > a {
    padding: 20px;
    @include mixins.boxFlex(center, center, column);

    .nav-article__button {
      white-space: normal;
    }
  }
}

.header__user {
  .flex {
    --gap-flex: 20px;
  }
}

.header__user > ul > .header__user-icon {
  &-account,
  &-message {
    @include mixins.mediaCustom("max-width: 1099px") {
      display: none;
    }
  }

  &-message svg {
    stroke-width: 2px;
  }

  &-cart button {
    @include mixins.clickEffect;
  }
}

#js-wishtlist a {
