@use '../abstracts/variables';
@use '../abstracts/mixins';

.top__bar {
  z-index: 1;
  width: 100%;
  background: variables.$color-background;
  color: variables.$color-title;
  padding: 8px 0;

  svg {
    width: 10px;
    height: 10px;
    position: absolute;
    z-index: 2;
    right: 8px;
    top: 9px;
    cursor: pointer;
  }

  &--content {
    align-items: center;
    @include mixins.boxFlex(initial);

    strong {
      font-weight: 600;
    }
  }

  &--message {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    overflow: hidden;
    line-height: 18px;

    @include mixins.media('tablet-l') {
      line-height: initial;
    }

    p {
      margin: 0 20px;
      text-align: center;
    }

    a {
      color: currentColor;
      text-decoration: none;
      @include mixins.linkEffect(currentColor, 1px);

      &:after {
        bottom: 4px;
      }
    }
  }
}
