@use "../abstracts/variables";
@use "../abstracts/mixins";

%wrapper {
  max-width: variables.$max-width--wrapper;
  margin-left: auto;
  margin-right: auto;
  padding-right: variables.$paddings-side--movil;
  padding-left: variables.$paddings-side--movil;
}

.wrapper {
  &-xl {
    @extend %wrapper;
    @include mixins.media("tablet-l") {
      padding-right: variables.$paddings-side-m;
      padding-left: variables.$paddings-side-m;
    }

    @include mixins.media("desktop") {
      padding-right: variables.$paddings-side-l;
      padding-left: variables.$paddings-side-l;
    }

    @include mixins.media("desktop-m") {
      padding-right: variables.$paddings-side-xl;
      padding-left: variables.$paddings-side-xl;
    }
  }

  &-l {
    @extend %wrapper;
    @include mixins.media("tablet-l") {
      padding-right: variables.$paddings-side-l;
      padding-left: variables.$paddings-side-l;
    }
  }

  &-xm {
    @extend %wrapper;
    @include mixins.media("tablet") {
      padding-right: variables.$paddings-side-m;
      padding-left: variables.$paddings-side-m;
    }
    @include mixins.media("desktop") {
      padding-right: variables.$paddings-side-xm;
      padding-left: variables.$paddings-side-xm;
    }
  }

  &-m {
    @extend %wrapper;

    @include mixins.media("tablet-l") {
      padding-right: variables.$paddings-side-m;
      padding-left: variables.$paddings-side-m;
    }
  }

  &-s {
    @extend %wrapper;
    @include mixins.media("tablet-l") {
      padding-right: variables.$paddings-side-s;
      padding-left: variables.$paddings-side-s;
    }
  }

  &-without-limit {
    @include mixins.media("tablet-l") {
      padding-right: variables.$paddings-side-s;
      padding-left: variables.$paddings-side-s;
    }
  }

  &-sl {
    @extend %wrapper;
    @include mixins.media("tablet") {
      padding-right: variables.$paddings-side-s;
      padding-left: variables.$paddings-side-s;
    }
    @include mixins.media("tablet-l") {
      padding-right: variables.$paddings-side-m;
      padding-left: variables.$paddings-side-m;
    }
    @include mixins.media("desktop-m") {
      padding-right: variables.$paddings-side-l;
      padding-left: variables.$paddings-side-l;
    }
  }
}

.section-space {
  margin-bottom: variables.$margin-section--movil;

  @include mixins.media("tablet") {
    margin-bottom: variables.$margin-section;
  }

  &--hero {
    margin-bottom: variables.$margin-section--hero-movil;

    @include mixins.media("tablet") {
      margin-bottom: variables.$margin-section--hero;
    }
  }

  &--top {
    margin-top: variables.$margin-section--movil;

    @include mixins.media("tablet") {
      margin-top: variables.$margin-section;
    }
  }
}
