@use "../abstracts/variables";
@use "../abstracts/mixins";

.hero--simple {
  position: relative;
  padding-bottom: 86vh;

  @include mixins.media('tablet-l') {
    padding-bottom: 100vh;
  }

  & > .hero__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .text-buttons > h1,
  .text-buttons > h2 {
    font-family: variables.$font-family--title;
  }

  .text-buttons > h1,
  .text-buttons > h2 {
    margin-bottom: 6px;
    font-size: variables.$font-size--mobile-h1;
    line-height: variables.$line-height--mobile-h1;

    @include mixins.media('tablet-l') {
      font-size: variables.$font-size--desk-h1;
      line-height: variables.$line-height--desk-h1;
    }
  }

  .text-buttons__p {
    font-size: variables.$font-size--general-m;
    line-height: variables.$line-height--general-m;
    margin-bottom: 4px;

    ul {
      list-style-position: inside;
    }
  }

  .hero__description {
    position: absolute;
    padding: 80px 16px;
    max-width: 601px;
    width: 100%;

    @include mixins.media('tablet') {
      padding: 150px 50px;
      width: max-content;
    }

    .h2 {
      margin-bottom: 10px;
      line-height: 1.4em;

      @include mixins.mediaCustom('max-width: 750px') {
        margin-bottom: 5px;
      }
    }

    &-container {
      margin: 0 auto;
    }

    a:not(a.btn-link) {
      @include mixins.linkEffect(currentColor, 1px);

      &::after {
        bottom: 4px;
      }
    }
  }

  /* Content background */
  &.white_bg .hero__description-container {

    @include mixins.media('tablet') {
      padding: 20px 20px 0;
      background-color: variables.$color-white;
    }

    &.button-less {
      @include mixins.media('tablet') {
        padding: 20px 20px;
      }
    }
  }

  &.mobile_white_bg .hero__description-container {
    @include mixins.mediaCustom('max-width: 749px') {
      padding: 12px 11px;
      background-color: variables.$color-white;
    }
  }

  /* Content positions */
  &.left_desk .hero__description {
    left: 0;
  }

  &.right_desk .hero__description {
    right: 0;
  }

  &.up_desk .hero__description {
    top: 50px;
  }

  &.down_desk .hero__description {
    bottom: 0;
  }

  &.middle_desk_y .hero__description {
    // Centered in the axis Y
    @include mixins.media('tablet') {
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &.middle_desk_x .hero__description {
    // Centered in the axis X
    @include mixins.media('tablet') {
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &.middle_desk_y.middle_desk_x .hero__description {
    // Centered in the both axis
    @include mixins.media('tablet') {
      transform: translate(-50%, -50%);
    }
  }

  &.middle_mobile .hero__description,
  &.down_mobile .hero__description {
    @include mixins.mediaCustom('max-width: 749px') {
      left: 50%;
      transform: translate(-50%, 0);
    }
  }

  &.middle_mobile .hero__description {
    @include mixins.mediaCustom('max-width: 749px') {
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &.down_mobile .hero__description {

    @include mixins.mediaCustom('max-width: 500px') {
      bottom: 40px;
    }

    @include mixins.mediaCustom('max-width: 749px') {
      bottom: 30px;
    }
  }

  .hero__info-text {
    bottom: 63px;
    right: 50%;
    position: absolute;
    width: 90%;
    transform: translateX(50%);
    font-size: variables.$font-size--general-s;
    line-height: variables.$line-height--general-s;
    text-align: center;
    @include mixins.excerptForLine(2);

    @include mixins.media('tablet') {
      bottom: 57px;
    }

    @include mixins.media('tablet-l') {
      bottom: 67px;
      width: auto;
    }
  }

  .btn-link {
    margin-top: -6px;
    transform: translateY(50%);
  }

  .btn-link:not(.mobile) {
    @include mixins.mediaCustom('max-width: 749px') {
      display: none;
    }
  }

  .btn-link.mobile {
    bottom: 0;
    left: 50%;
    position: absolute;
    transform: translate(-50%, 50%);

    @include mixins.media('tablet') {
      display: none;
    }
  }

  .btn-1 .icon-arrow {
    display: none;
  }

  .hero__sticker {
    position: absolute;
    top: 160px;
    right: 0;
    height: auto;
    width: 113px;

    @include mixins.media('tablet') {
      right: 0;
      height: auto;
      width: 140px;
    }

    @include mixins.media('tablet-l') {
      right: 0;
      height: 269px;
      width: 180px;
    }

    &.mobile-hidden {
      @include mixins.mediaCustom('max-width: 991px') {
        display: none;
      }
    }
  }
}

.hero--slider {
  .swiper-wrapper {
    @include mixins.mediaCustom('max-width: 749px') {
      padding-bottom: 50px;
    }
  }

  .swiper-pagination {
    position: absolute;
    z-index: 1;
    bottom: 20px;
    display: flex;
    width: 100%;
    justify-content: center;

    @include mixins.mediaCustom('max-width: 749px') {
      bottom: 88px;
    }
  }
}
